import React, {useState, useEffect} from "react";
import "./NavBar.css";
import { Nav, Navbar, Container } from "react-bootstrap";
import {scroller} from 'react-scroll';

export default function NavBar() {

  function goHome(){
    scroller.scrollTo('home')
  }

  function goProjects(){
    scroller.scrollTo('projects')
  }

  function goContact(){
    scroller.scrollTo('contact')
  }

  // function goResume(){
  //   scroller.scrollTo('resume')
  // }

  function goSkills(){
    scroller.scrollTo('skills')
  }
  
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container className="navbar-width">
          <Navbar.Brand onClick={goHome}>Lynn's Portfolio</Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto"></Nav>
            <Nav>
              <Nav.Link onClick={goSkills}>Skills</Nav.Link>
              <Nav.Link onClick={goProjects}>Projects</Nav.Link>
              <Nav.Link onClick={goContact}>Contact</Nav.Link>
              {/* <Nav.Link onClick={goResume}>Resume</Nav.Link> */}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
}
